import React from 'react';
import {Row, Col, Card, Table, Form, Modal, Button, InputGroup, FormControl, DropdownButton, Dropdown, Tabs, Tab} from 'react-bootstrap';
import MainCard from "../../App/components/MainCard";
import Aux from "../../hoc/_Aux";
import AmChartEarnings from "../Widget/Chart/AmChartEarnings";
import DEMO from "../../store/constant";
import { Redirect, Switch, Route, Link } from "react-router-dom";
import IntegrationEvents from "../../App/components/IntegrationEvents";
import WorkflowEvents from "../../App/components/WorkflowEvents";
import NotificationsTabs from "../../App/components/NotificationsTabs";
import avatar1 from '../../assets/images/user/avatar-1.jpg';
import avatar2 from '../../assets/images/user/avatar-2.jpg';
import avatar3 from '../../assets/images/user/avatar-3.jpg';
import avatar8 from '../../assets/images/user/avatar-8.jpg';

import shape1 from '../../assets/images/widget/shape1.png';
import shape2 from '../../assets/images/widget/shape2.png';
import shape3 from '../../assets/images/widget/shape3.png';






import AmChartStatistics6 from "../Widget/Chart/AmChartStatistics6";


class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          islogout: false
        };
        this.state = {
            currentDateTime: Date().toLocaleString()
          }
      }
      signOut = () => {
        localStorage.removeItem("token");
        this.setState({
          islogout: true
        });
      };
    state = {
        loadCard: false
    }

    componentDidMount() {
        setInterval(() => {
          this.setState({
            currentDateTime: Date().toLocaleString()
          })
        }, 1000)
      }

    cardReloadHandler = () => {
        this.setState({loadCard: true});
        setInterval(() => {
            this.setState({loadCard: false});
        }, 2500);
    };



    render() {

        let loader, ActionRight;
        let card = '';
        let cardClass = [];

        if (this.state.loadCard) {
            cardClass = [...cardClass, 'card-load'];
            loader = (
                <div className="card-loader">
                    <i className="pct-loader1 anim-rotate"/>
                </div>
            );
        }
        if (this.props.cardClass) {
            cardClass = [...cardClass, this.props.cardClass];
        }

        ActionRight = (
                <div className="ActionRight">
                    <Dropdown alignRight={true} className="">
                        <Dropdown.Toggle id="" className="">
                            <i className="feather icon-more-vertical"/>
                        </Dropdown.Toggle>
                        <Dropdown.Menu as='ul' className="list-unstyled card-option">
                            <Dropdown.Item as='li' className="dropdown-item">
                                <i className='feather icon-info'/>
                                <a onClick={() => this.setState({ showModalLog:true, showModalWF1:false, showModalWF2:false, showModalWF3:false, showModalWF4:false})}>Details</a>
                                    <Modal size="xl" show={this.state.showModalLog} onHide={() => this.setState({ showModalLog:false})} dialogClassName={"primaryModal"}>
                                        <Modal.Header closeButton closeLabel="close window">

                                            <Modal.Title></Modal.Title>
                                        </Modal.Header>

                                        <Modal.Body>
                                            <h5>SAP HCM - SF</h5>
                                            <h2 className="title">Integration Scenarios Events</h2>
                                            <h5>Deployed on 01/12/2020 04:17am CET</h5>
                                            <div className="LogDetails">
                                                <h6 className="text-monitor-error"><i className="fa fa-circle text-c-warning f-15 m-r-5"/>ERRONEOUS</h6>
                                                <p>OrgStructureUS</p>
                                                <p>WDtPDHEF456ORGUS</p>
                                                <p>Created by Alex Mindge</p>
                                                <p>Run 01/12/2020 10:18am CET</p>
                                            </div>
                                            <Col md={12} className='mt-xl-5'>
                                                <h4 class="float-left">Log details</h4>
                                                <button type="button" class="btn btn-outline-secondary btn-sm float-right mb-4" onClick={this.cardReloadHandler}><i class="feather icon-refresh-cw"></i>Reprocess scenario</button>
                                            </Col>
                                            <Col md={12} className='mb-xl-5 Recent-Users small_font'>
                                                <Table responsive hover>
                                                    <thead>
                                                    <tr>
                                                        <th scope="col">Status<i class="feather icon-chevron-down"></i> <i class="feather icon-chevron-up"></i></th>
                                                        <th scope="col">Started <i class="feather icon-chevron-down"></i> <i class="feather icon-chevron-up"></i></th>
                                                        <th scope="col">System<i class="feather icon-chevron-down"></i> <i class="feather icon-chevron-up"></i></th>
                                                        <th scope="col">Error reason<i class="feather icon-chevron-down"></i> <i class="feather icon-chevron-up"></i></th>
                                                        <th scope="col">Error message</th>
                                                        <th scope="col">Error details</th>
                                                        <th scope="col">Workflow</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                    <tr className="unread">
                                                        <td>
                                                            <h6 className="text-monitor-error"><i className="fa fa-circle text-c-warning f-15 m-r-5"/>ERRONEOUS</h6>
                                                        </td>
                                                        <td>
                                                            <h6 className="mb-1">05/12/2021</h6>
                                                            <h6 className="mb-1">11:01am CET</h6>
                                                        </td>
                                                        <td>
                                                            <h6 className="mb-1">WorkDay</h6>
                                                        </td>
                                                        <td>
                                                            <h6 className="mb-1">04 - Warning</h6>
                                                        </td>
                                                        <td>
                                                        <h6 className="mb-1">YRC10CDD - Cost Center Integration</h6>
                                                        </td>
                                                        <td>
                                                            <h6 className="mb-1">The system does not have any BAdI implementation assigned</h6>
                                                        </td>
                                                        <td>
                                                            <h6 className="mb-1">Triggered</h6>
                                                        </td>
                                                    </tr>
                                                    </tbody>
                                                </Table>
                                            </Col>
                                        </Modal.Body>
                                    </Modal>
                            </Dropdown.Item>
                            <Dropdown.Item as='li' className="dropdown-item" onClick={this.cardReloadHandler}>
                                <i className='feather icon-refresh-cw'/>
                                <a href={DEMO.BLANK_LINK}> Reprocess </a>
                            </Dropdown.Item>
                            <Dropdown.Item as='li' className="dropdown-item">
                                <i className='feather icon-pause-circle'/>
                                <a href={DEMO.BLANK_LINK}> Stop </a>
                            </Dropdown.Item>

                        </Dropdown.Menu>
                    </Dropdown>
                </div>
            );

        if (this.state.islogout) {
            return <Redirect to="/auth/signin-2" />;
            }
            const { match } = this.props;
            return (
            <Aux>
                <Row>
                <Col xl={12}>
                <h2 className="mb-4">Welcome back, Robert</h2>
                </Col>
                <Col xl={9}>
                <h3 className="">58 active scenarios</h3>
                </Col>
                <Col xl={3}>
                <button type="button" class="btn btn-outline-secondary btn-sm float-right" onClick={this.cardReloadHandler}><i class="feather icon-refresh-cw"></i>Refresh events</button>
                </Col>
                <Col xl={12}>

                <p><i className="feather icon-alert-octagon"/>  Current Time Settings : { this.state.currentDateTime }  <i className="feather icon-settings"/></p>

                </Col>
                <Col md={12} xl={5}>
                    <MainCard title='Erroneous runs' isOption erroneous>
                        <div className={cardClass.join(' ')} >
                            {loader}
                            <div className="dashboard_cardBodyPadding">
                                <Card.Body>
                                    <div className="row align-items-center justify-content-center">
                                        <div className="col-md-4 text-center">

                                        <div className="circle">
                                            <h2 className="text-c-red">3</h2>
                                        </div>
                                        </div>
                                        <div className="col-md-8 text-left">
                                            {/* <img src={shape2} style={{width: '80px'}} alt="activity-user" /> */}
                                            <p>Last execution 4 hour ago</p>
                                            <p>Run type : automatic</p>
                                        </div>
                                    </div>
                                </Card.Body>
                            </div>
                        </div>
                    </MainCard>
                    <MainCard title='Succesful runs' isOption success>
                        <div className={cardClass.join(' ')} >
                            {loader}
                            <div className="dashboard_cardBodyPadding">
                                <Card.Body>
                                    <div className="row align-items-center justify-content-center">

                                        <div className="col-md-4 text-center">

                                            <h2 className="text-c-green">51</h2>


                                        </div>
                                        <div className="col-md-8 text-left">
                                            {/* <img src={shape1} style={{width: '80px'}} alt="activity-user" /> */}
                                            <p>Last execution 2 days ago</p>
                                            <p>Run type : manual</p>
                                        </div>
                                    </div>
                                </Card.Body>
                            </div>
                        </div>
                    </MainCard>



                    <MainCard title='Not deployed' isOption notdeployed>
                        <div className={cardClass.join(' ')} >
                            {loader}
                            <div className="dashboard_cardBodyPadding">
                                <Card.Body>
                                    <div className="row align-items-center justify-content-center">
                                        <div className="col-md-4 text-center">
                                            <h2 className="text-c-grey">4</h2>
                                        </div>
                                        <div className="col-md-8 text-left">
                                            {/* <img src={shape3} style={{width: '80px'}} alt="activity-user" /> */}
                                            <p>Last setup 1 week ago</p>
                                            <p>Name : WDtoPDIntSce</p>
                                        </div>
                                    </div>
                                </Card.Body>
                            </div>
                        </div>
                    </MainCard>

                </Col>
                <Col md={12} xl={7}>
                    <NotificationsTabs></NotificationsTabs>
                </Col>
                <IntegrationEvents></IntegrationEvents>
                <WorkflowEvents></WorkflowEvents>





                </Row>
            </Aux>
        );
    }
}

export default Dashboard;